/* =======================================
1- Extra small devices (portrait phones, less than 576px)
========================================*/

/* === full page ===*/
html,
body {
  height:100%;
  width:100%;
 
  overflow-y: visible;
  overflow-x: hidden;
}
.background {
  background-color: #232741;

  min-height: 100vh;
  min-width: 100vw;
}

/* === page-container ===*/
.page-container {
  margin-bottom: 3vh;
  margin-top: 2vh;
  margin-right: 0vw;
  margin-left: 0vw;

  padding-right: 0px;
  padding-left: 0px;
}
.left-side,
.right-side {
  width: calc(100% - 12px);
  padding: 6px; 
  float: left;
}

/* === left-side ===*/
.left-side {
}
.brand {
  min-height: 20vh;
  margin-top: 0vh;
  
  color: #fff;  
  text-align: left;
}

/* === right-side ===*/
.wall {
  overflow-y: hidden;
  height: 100%;
  width: 100%;
  
  background-color: #fff;
  color: #000;

  border-radius: 15px;
}

/* === wall content ===*/
.wall .section {
  padding: 20px;

  text-align: center;
}
/* intro */
